<template>
    <div id="about">
      <div class="container">
        <h2>{{currentLanguage.a1}}</h2>
        <p class="para">
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{currentLanguage.a2}}
        </p>
        <h2>{{currentLanguage.a3}}</h2>
        <p>Email: service@fanmi.com</p>
        <p>Tel: 028-85142302</p>
      </div>
    </div>
</template>

<script>
  import { mapGetters } from 'vuex'
    export default {
        name: "about",
      data () {
          return {
            currentLanguage: null,
            chinese:{
              a1: '关于我们',
              a2: '成都壹比特旨在打造以壹比特文明形象为代表的硬件设备品牌制造商。壹比特矿机管理团队由数位芯片、显卡、互联网及资本运营领域的资深人士构成，具有广阔的全球视野和优秀的战略规划及执行能力。壹比特矿机成立于2017年，利用优越的地理优势，迅速整合专业人才和生产资源，成功打造挖矿领域多项核心竞争力，我们更面向全球市场，致力于为挖矿全产业链持续输送价值。 壹比特矿机自2017年4月开始研发，9月开始多方面实验，通过总结市场现有显卡挖矿的优势，我们的工程团队全面完善了显卡参数。数千个小时的电脑模拟和精细微调让我们获得了前所未有的低功耗及超高算力。Onebit基于自主开发的尖端显卡配置，我们的目标是在不断推动挖矿行业经济增长。',
              a3: '联系方式',
            },
            english: {
              a1: 'About Us',
              a2: 'Chengdu OneBit aims to build a hardware equipment brand manufacturer represented by the OneBit civilization image. The OneBit mining management team consists of digital chips, graphics cards, Internet and capital operations veterans with a broad global vision and excellent strategic planning and execution capabilities. Established in 2017, OneBit Mining Machinery utilizes superior geographical advantages, rapidly integrates professional talents and production resources, and successfully builds a number of core competitiveness in the mining field. We are more oriented to the global market and are committed to continuously delivering value to the entire mining industry chain. . The OneBit mining machine began research and development in April 2017, and began various experiments in September. By summarizing the advantages of existing graphics card mining in the market, our engineering team has comprehensively improved the graphics card parameters. Thousands of hours of computer simulation and fine-tuning have given us unprecedented low power consumption and high computing power. Onebit is based on self-developed cutting-edge graphics card configuration, and our goal is to continuously promote the economic growth of the mining industry.',
              a3: 'Contact Us'
            }
          }
      },
      computed: {
        ...mapGetters(['language'])
      },
      created () {
        if (this.language === "中文"){
          this.currentLanguage = this.chinese
        }
        if (this.language === "english"){
          this.currentLanguage = this.english
        }
      },
      watch: {
        language (val) {
          if (val === "中文"){
            this.currentLanguage = this.chinese
          }
          if (val === "english"){
            this.currentLanguage = this.english
          }
        }
      }
    }
</script>

<style lang="scss" scoped>
#about{
  background: #1C1D21;
  color: #fff;
  padding-bottom: 50px;
  h2{
    padding-top: 50px;
    padding-bottom: 20px;
  }
}
</style>



