<template>
    <div id="header">
      <div class="container">
        <div class="left name">
          <img src="../assets/logo.png"/>
          <!--凡米科技-->
        </div>
        <div class="right">
          <ul>
            <li><a href="#banner">{{currentLanguage.a1}}</a></li>
            <li><a href="#product">{{currentLanguage.a2}}</a></li>
            <li><a href="#about">{{currentLanguage.a3}}</a></li>
            <li @mouseenter="showLanguage=true" @mouseleave="showLanguage=false" class="language">
              <a  href="javascript:;">{{currentLanguage.a4}}</a>
              <ul v-show="showLanguage" class="languages">
                <li @click="changeToEnglish"><a href="javascript:;" >English</a></li>
                <li @click="changeToChinese"><a href="javascript:;" >中文</a></li>
              </ul>
            </li>
          </ul>
        </div>
      </div>
    </div>
</template>

<script>
  import { mapGetters } from 'vuex'
    export default {
        name: "header",
      data () {
          return{
            showLanguage:false,
            currentLanguage: null,
            chinese:{
              a1: '首页',
              a2: '产品详情',
              a3: '关于我们',
              a4: '多语言'
            },
            english: {
              a1: 'HOME',
              a2: 'PRODUCT',
              a3: 'ABOUT',
              a4: 'TRANSLATION'
            }
          }
      },
      computed: {
        ...mapGetters(['language'])
      },
      created () {
        if (this.language === "中文"){
          this.currentLanguage = this.chinese
        }
        if (this.language === "english"){
          this.currentLanguage = this.english
        }
      },
      methods: {
        changeToEnglish () {
          this.$store.dispatch("setLanguage","english")
        },
        changeToChinese () {
          this.$store.dispatch("setLanguage","中文")
        },
      },
      watch: {
          language (val) {
            if (val === "中文"){
              this.currentLanguage = this.chinese
            }
            if (val === "english"){
              this.currentLanguage = this.english
            }
          }
      }
    }
</script>

<style lang="scss" scoped>
#header{
  z-index: 9999;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 80px;
  background: #000;
  color: #fff;
  .container{
    width: 1140px;
    margin: auto;
    .left{
      float: left;
      font-size: 30px;
    }
    .right{
      float: right;
      ul{
        li{
          list-style: none;
          float: left;
          line-height: 80px;
          margin-left: 30px;
          cursor: pointer;
          font-size: 21px;
          a{
            text-decoration: none;
            color: #fff;
          }
        }
        .language{
          position: relative;
          .languages{
            position: absolute;
            left: -13px;
            top: 80px;
            background: #000;
            color: #fff;

            li{
              width: 100px;
              list-style: none;
              text-align: center;
              font-size: 18px;
              height: 50px;
              line-height: 50px;
              margin: 0;
              a{
                width: 100%;
                height: 100%;
                text-decoration: none;
                color: #fff;
                text-align: center;
              }
            }
          }
        }
      }
    }
    .name{
      margin-top: 20px;
    }
  }

}
</style>



