<template>
    <div id="product">
      <div class="container">
        <div class="description">
          <div class="name">{{currentLanguage.a1}}</div>
          <div class="title">{{currentLanguage.a2}}</div>
          <div class="content">
            {{currentLanguage.a3}}<br/>
            {{currentLanguage.a4}}<br/>
            {{currentLanguage.a5}}<br/>
            {{currentLanguage.a6}}</div>
          <img src="../assets/2.png">
        </div>
      </div>
    </div>
</template>

<script>
  import { mapGetters } from 'vuex'
    export default {
        name: "product",
      data () {
          return {
            currentLanguage: null,
            chinese:{
              a1: '矿机介绍',
              a2: '定制化，高效率的矿机',
              a3: '显卡矿机是支持多种挖矿算法的挖矿装置。不同于普通显卡，显卡矿机是通过多张高配置显卡，',
              a4: '定制化、高兼容的机箱，以及专业的矿机架等专门针对挖矿需求优化设计的设备组装而成。',
              a5: '显卡矿机极具灵活性，能针对不同的虚拟货币运行不同的算法，做到一机多用。',
              a6: '在矿机更新换代速度日益放缓且价格日渐增高的今天，显卡矿机不失为更好的投资选择',
            },
            english: {
              a1: 'Mining machine introduction',
              a2: 'Customized, efficient mining machine',
              a3: 'The graphics card mining machine is a mining device that supports multiple mining algorithms. Unlike ordinary graphics cards, the graphics card mining machine is through multiple high-profile graphics cards,',
              a4: 'Customized, highly compatible chassis, as well as professional mine racks and other equipment specifically designed for mining needs.',
              a5: 'The graphics card mining machine is very flexible, can run different algorithms for different virtual currencies, and achieve multi-purpose',
              a6: 'Today, the speed of the mining machine is slowing down and the price is increasing day by day. The graphics card mining machine is a better investment choice.',
            }
          }
      },
      computed: {
        ...mapGetters(['language'])
      },
      created () {
        if (this.language === "中文"){
          this.currentLanguage = this.chinese
        }
        if (this.language === "english"){
          this.currentLanguage = this.english
        }
      },
      watch: {
        language (val) {
          if (val === "中文"){
            this.currentLanguage = this.chinese
          }
          if (val === "english"){
            this.currentLanguage = this.english
          }
        }
      }

    }
</script>

<style lang="scss" scoped>
  #product{
    background: #222327;
    .description{
      color: #fff;
      text-align: center;
      padding: 100px;
    }
    .description > .name{
      font-size: 30px;
      padding-bottom: 30px;
    }
    .description> .title{
      font-size: 50px;
      padding-bottom: 50px;
    }
    .description>img{
      padding-top: 100px;
    }
  }
</style>



