<template>
    <div id="banner">
      <img src="../assets/1.png"/>
      <div class="content">
        <div class="name">{{currentLanguage.a1}}</div>
        <div class="title">{{currentLanguage.a2}}</div>
      </div>
    </div>
</template>

<script>
  import { mapGetters } from 'vuex'
    export default {
        name: "banner",
        data () {
          return {
            currentLanguage: null,
            chinese:{
              a1: 'OneBit矿机',
              a2: '快速，稳定，节能的新一代矿机'
            },
            english: {
              a1: 'OneBit Mining machine',
              a2: 'Fast, stable and energy-saving new generation mining machine'
            }
          }
        },
      computed: {
        ...mapGetters(['language'])
      },
      created () {
        if (this.language === "中文"){
          this.currentLanguage = this.chinese
        }
        if (this.language === "english"){
          this.currentLanguage = this.english
        }
      },
      watch: {
        language (val) {
          if (val === "中文"){
            this.currentLanguage = this.chinese
          }
          if (val === "english"){
            this.currentLanguage = this.english
          }
        }
      }
    }
</script>

<style lang="scss" scoped>
#banner{
  width: 100%;
  position: relative;
  img{
    width: 100%;
  }
  .content{
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    text-align: center;
    padding-top: 200px;
    color: #fff;
    .name{
      font-size: 50px;
      margin-bottom: 20px;
    }
    .title{
      font-size: 30px;
    }
  }
}
</style>



